.was-done, .nothing-was-done
  display: none

.jst-card-title
  margin-right: 10px !important

#custom-rating-grifus-form
  width: 100% !important

#process
  display: none

#film-title, #film-rating
  display: flex
  align-items: center
  font-size: 16px
  margin: 15px 0px 26px 0px

.film-revised, .film-counter
  font-size: 18px
  margin: 6px 4px 4px 6px

#film-rating-badge
  margin-left: 1.5px

#spinner-grifus
  display: none
  float: right

.grifus-span
  margin-left: 8px

.movie-icon
  color: #D91E18

.info-icon
  color: #dc143c
  vertical-align: middle
  font-size: 20px

.picture-icon
  color: #446CB3

.success-icon
  color: #50b344

.g-icons
  font-size: 35px !important

.mdl-card__supporting-text
  font-size: 1.1rem !important

.replace-button
  margin-top: 10px

.mdl-checkbox
  display: inline-table

@media (min-width: 840px)
  .jst-card
    max-width: 429px !important

@media (max-width: 370px)
  .jst-card-subtitle
    font-size: 16px !important

@media (max-width: 455px)
  .grifus-span
    font-size: 15px
